import React from 'react';

export default class ImgText extends React.Component {
  constructor (props) {
    super (props);
  }
  render () {
    return (
    <div className="img-text-wrapper">
      <img src={this.props.img} alt=""/>
      <div>
        {this.props.children}
      </div>
    </div>
    )
  }
}